import './App.css';
import Data from './Data.json'


import React from 'react'
import Main from './components/main/Main'
import Modal from './components/modal/Modal'
import Agenda from './components/agenda/Agenda'
import LogIn from './components/signIn/LogIn'
import Gallery from './components/gallery/Gallery'


export const Sections = React.createContext()

class App extends React.Component {
  constructor(props) {
    super(props)
  
    this.state = {
      show: true,
      eventDetails: Data[0],
      section: Data[1],
      agenda: Data[3],
      gallery: Data[4]
       
    }
  }

  
  getClickedValue = hide => this.setState({show: hide})
    
  
  
  
  render() {
    
    const agenda = <Agenda agendaDetails={this.state.agenda.agenda}/>
    const login = <LogIn />
    const modalContent = agenda
    return (
      <div className="container-fluid">
       <Sections.Provider value = {this.state.section.sections}> 
        <Main events = { this.state.eventDetails.events[0]} val = {this.state.section} /> 
       </Sections.Provider>
       <Gallery stallDetails = {this.state.gallery.stall}/>
        
       { this.state.show ? <Modal closeModal = { this.getClickedValue}>{modalContent}</Modal> : ''}
        
      </div>
    )
  }
}

export default App
